.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
:root .ant-slider-dot-active {
  border-color: var(--antd-slider-active) !important;
}
:root .ant-slider-handle:focus {
  border-color: var(--antd-slider-focus);
  box-shadow: 0 0 0 5px var(--antd-slider-focus-shadow);
}
:root .ant-picker-focused,
:root .ant-select-focused.ant-select-single:not(.ant-select-customize-input) .ant-select-selector,
:root .ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled):focus-within,
:root .ant-radio-input:focus + .ant-radio-inner,
:root .ant-select-focused.ant-select-multiple .ant-select-selector,
:root .ant-input-affix-wrapper:focus,
:root .ant-input-affix-wrapper-focused,
:root .ant-input:focus,
:root .ant-input-focused,
:root .ant-input-number:focus,
:root .ant-input-number-focused,
:root .ant-cascader-picker:focus .ant-cascader-input {
  box-shadow: 0 0 0 2px var(--antd-select-focus-shadow);
}
:root .ant-picker-cell-in-view.ant-picker-cell-range-hover-start:not(.ant-picker-cell-in-range):not(.ant-picker-cell-range-start):not(.ant-picker-cell-range-end)::after,
:root .ant-picker-cell-in-view.ant-picker-cell-range-hover-end:not(.ant-picker-cell-in-range):not(.ant-picker-cell-range-start):not(.ant-picker-cell-range-end)::after,
:root .ant-picker-cell-in-view.ant-picker-cell-range-hover-start.ant-picker-cell-range-start-single::after,
:root .ant-picker-cell-in-view.ant-picker-cell-range-hover-end.ant-picker-cell-range-end-single::after,
:root .ant-picker-cell-in-view.ant-picker-cell-range-hover:not(.ant-picker-cell-in-range)::after {
  border-top: 1px dashed var(--primary-lightener);
  border-bottom: 1px dashed var(--primary-lightener);
}
:root tr > .ant-picker-cell-in-view.ant-picker-cell-range-hover:last-child::after,
:root tr > .ant-picker-cell-in-view.ant-picker-cell-range-hover-start:last-child::after,
:root .ant-picker-cell-in-view.ant-picker-cell-range-hover-edge-end:not(.ant-picker-cell-range-hover-edge-end-near-range)::after,
:root .ant-picker-cell-in-view.ant-picker-cell-range-hover-end::after {
  border-right: 1px dashed var(--primary-lightener);
}
:root tr > .ant-picker-cell-in-view.ant-picker-cell-range-hover:first-child::after,
:root tr > .ant-picker-cell-in-view.ant-picker-cell-range-hover-end:first-child::after,
:root .ant-picker-cell-in-view.ant-picker-cell-range-hover-edge-start:not(.ant-picker-cell-range-hover-edge-start-near-range)::after,
:root .ant-picker-cell-in-view.ant-picker-cell-range-hover-start::after {
  border-left: 1px dashed var(--primary-lightener);
}
:root .ant-dropdown-menu-item {
  padding: 5px 12px;
  min-width: 140px;
  display: flex;
  align-items: center;
}
:root .ant-card .ant-card-head {
  padding: 0 31px;
}
:root .ant-card.fat-card .ant-card-head {
  border: 0;
}
:root .ant-card.fat-card .ant-card-head .ant-card-head-title,
:root .ant-card.fat-card .ant-card-head .ant-card-extra {
  padding: 20px 0 0;
}
:root .ant-card.fat-card .ant-card-head .ant-card-head-title,
:root .ant-card.fat-card .ant-card-head .ant-card-head-title > p {
  font-size: 14px;
  font-weight: 500;
}
:root .ant-card.fat-card .ant-card-body {
  padding: 22px 31px 29px 31px;
}
:root .ant-card.thin-card .ant-card-head {
  min-height: 36px;
  border-bottom-color: #f5f5f5;
}
:root .ant-card.thin-card .ant-card-head .ant-card-head-title {
  padding: 6px 0px;
}
:root .ant-card.thin-card .ant-card-body {
  padding: 12px 24px;
}
:root .ant-select {
  margin-right: 16px;
  width: 100px;
}
:root .ant-select .ant-select-selection {
  border-radius: 2px;
}
:root .ant-select .ant-select-selection__placeholder {
  font-size: 12px;
}
:root .ant-form-item-children .ant-select {
  margin-right: auto;
}
:root .ant-progress-bg {
  background-image: linear-gradient(45deg, rgba(255, 255, 255, 0.15) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.15) 50%, rgba(255, 255, 255, 0.15) 75%, transparent 75%, transparent);
  background-size: 1rem 1rem;
  -webkit-animation: progress-bar-stripes 1s linear infinite;
          animation: progress-bar-stripes 1s linear infinite;
}
:root .ant-table-bordered.ant-table-empty .ant-table-placeholder {
  border-right: 1px solid #f2f2f2;
  border-left: 1px solid #f2f2f2;
}
:root .ant-table-thead > tr > th {
  background-color: transparent;
}
:root .ant-progress.normal .ant-progress-bg {
  background-color: #308ee0;
}
:root .ant-progress.success .ant-progress-bg {
  background-color: #00ce68;
}
:root .ant-progress.danger .ant-progress-bg {
  background-color: #ffaf00;
}
:root .ant-progress.warning .ant-progress-bg {
  background-color: #ffaf00;
}
@-webkit-keyframes progress-bar-stripes {
  0% {
    background-position: 1rem 0;
  }
  100% {
    background-position: 0 0;
  }
}
@keyframes progress-bar-stripes {
  0% {
    background-position: 1rem 0;
  }
  100% {
    background-position: 0 0;
  }
}
